.others__container{
    margin:2em 0em;
    display: flex;
    flex-wrap: wrap;
}

.others__section{
    margin-bottom:2em;
    width: 30%;
}

.others__content{
    width:70%;
    display: flex;
    flex-wrap: wrap;
    gap:2em;
}

.individual__project{
    width:35%;
    line-height:1.3;
}

.project__image{
    width:100%;
}

@media only screen and (max-width: 800px){
    .others__section{
        width:100%;
    }

    .others__container{
        width:100%;
    }

    .others__content{
        width:100%;
    }

    .individual__project{
        width:100%;
    }
}