.design__container{
    text-align: center;
}

.design__title{
    font-size:2em;
    margin:2em 0em 0.5em 0em;
}

.design__description{
    width:30%;
    margin:0 auto;
    line-height: 1.3;

}

.image__container{
    width:100%;
    display: flex;
    flex-wrap:wrap;
    margin: 3em 0em;
    gap:2em;
    justify-content: space-around;
    align-items: flex-start;
}

.prototype__images{
    width:300px;
    object-fit: contain;
}

@media only screen and (max-width:1400px){
    .design__description{
        width:60%;
    }
}

@media only screen and (max-width:1000px){
    .design__description{
        width:80%;
    }
}