.container{
    width:100%;
    display: flex;
    flex-wrap:wrap;
    line-height: 1.3;
}

.problem__title{
    width:30%;
    margin-bottom:2em;
}

.problem__content{
    width:70%;
}

.problem__text{
    width:60%;
}

.problem__list{
    list-style-type: '- ';
}

.list__item{
    margin-bottom: 0.5em;
}

@media only screen and (max-width: 800px) {
    .problem__title, .problem__content, .problem__text{
        width:100%;
    }
}