.title__container{
    margin: 2em 0em;
    display: flex;
    flex-wrap: wrap;
}

.title{
    width: 30%;
    margin-bottom:1em;
}

.description__container{
    width: 70%;
    line-height: 1.3;
    margin-bottom:2em;
}

@media only screen and (max-width: 800px){
    .title{
        width:100%;
        font-size:1.8em;
    }

    .description__container{
        width:100%;
        font-size:1em;
    }
}