.container{
    width:100%;
    display:flex;
    flex-wrap:wrap;
}

.overview__title{
    width:30%;
    margin-bottom:2em;
}

.overview__content__master{
    width: 70%;
    display:flex;
    flex-wrap:wrap;
    gap:2em;
    justify-content: space-between;
}

.overview__content__container{
    width: 35%;
}

.overview__content__list__container{
    width:35%;
}

.list__container{
    width: 100%;
}

.overview__list{
    list-style-type: '- ';
    line-height:1.5;
    margin:0.5em 0em;
}

@media only screen and (max-width: 800px){
    .overview__title{
        width:100%;
    }

    .overview__content__master{
        width:100%;
    }
    .overview__content__container{
        width:90%;
    }
    .overview__content{
        width:100%;
    }

    .overview__content__list__container{
        width:100%;
    }

    .overview__list{
        width:100%;
    }

}