.skillsList__container{
    margin: 2em 0em;
    display: flex;
    flex-wrap: wrap;
}

.skillsList__title{
    width:30%;
    margin-bottom: 2em;
}

.skillsContent__container{
    width:70%;
    display: flex;
    flex-wrap:wrap;
    justify-content: space-between;
}

.skillsList{
    list-style-type: none;
    width:50%;
    margin:0em;
}

.skillsList__item{
    margin:0em 0em 0.5em 0em;
}

@media only screen and (max-width: 800px){
    .skillsList__title{
        width:100%;
    }
    .skillsList{
        width:100%;
    }
}