.container{
    display: flex;
    flex-wrap:wrap;
    width: 100%;
}

.insights__title{
    width:30%;
    margin-bottom:2em;
}

.insights__container{
    width:70%;
    display: flex;
    flex-wrap: wrap;
    gap:2em;
    justify-content: space-evenly;
}

.individual__insight{
    text-align: center;
    margin: 0 auto;
    width:25%;
}

.percent{
    font-size: 4em;
}

@media only screen and (max-width:780px) {
    .insights__title, .insights__container{
        width:100%;
    }
    
    .individual__insight{
        width:100%;
        display:flex;
        align-items: center;
        text-align: left;
        gap:1em;
    }
}
