.header{
    width:100%;
    display: flex;
    justify-content: space-between;
    align-content: center;
    margin: 2em 0em 0.6em 0em;
}

a{
    text-decoration: none;
}

a:hover{
    color:rgb(197, 197, 197);
}

.navigation{
    display:flex;
    width: 170px;
    justify-content: space-between;
}

.navigation__item{
    list-style-type: none;
    
}

.navigation__link{
    font-size:2em;
}

@media only screen and (max-width:900px){
    .navigation{
        width: 110px;
    }
    .logo, .navigation__link{
        font-size: 1.4em;
    }
} 