.App {
  text-align: center;
  width: 90%;
}

.header__text{
  width:50%;
  margin-bottom:3em;
  letter-spacing: -.005em;
  line-height: 1.3em;
}

.main__container{
  width:90%;
  margin: 0 auto;
}

.container{
  margin: 3em 0em;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media only screen and (max-width:1400px){
  .header__text{
    width:80%;
  }
}

@media only screen and (max-width:700px){
  .header__text{
    width:100%;
    font-size: 1.4em;
    text-align: center;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
