.results__container{
    line-height: 1.5;
    display: flex;
    flex-wrap:wrap;
    margin: 2em 0em;
}

.results__title{
    margin-bottom:1em;
    width:30%
}

.results__content{
    width:40%;
}

.takeaway__title{
    font-weight: 900;
    letter-spacing: 0.03em;
}

.takeaway__list{
    list-style-type: '- ';
}

@media only screen and (max-width: 800px){
    .results__title{
        width: 100%;
    }

    .results__content{
        width: 100%;
    }
}