.projectContent__container{
    display: flex;
    flex-wrap:wrap;
    margin: 3em 0em;
}

.projectContent__title{
    width:30%;
    margin-bottom:2em;
}

.content__container{
    width:70%;
    display: flex;
    flex-wrap:wrap;
}

.content__separator{
    width:50%;
}

.content__items{
    margin-bottom:1em;
    line-height: 1.3;
}

@media only screen and (max-width:600px){
    .projectContent__title{
        width:100%;
    }
    .content__separator{
        width:100%;
    }
}