.container{
    display: flex;
    flex-wrap:wrap;
}

.process__title{
    width: 30%;
    margin-bottom:2em;
}

.process__content{
    width:70%;
}

.process__description{
    width:60%
}

.process__image{
    width:100%;
}

@media only screen and (max-width: 800px){
    .process__title, .process__content, .process__description{
        width:100%;
    }
}