.educationList__container{
    display: flex;
    flex-wrap: wrap;
    margin: 3em 0em;
}

.educationList__title{
    width:30%;
    margin-bottom:2em;
}

.educationList__content__container{
    width:70%;
    display:flex;
    flex-wrap:wrap;
}

.eeList__item{
    margin-bottom: 1.5em;
    list-style-type: none;
    line-height: 1.2;
}

.eeList{
    width:50%;
}

@media only screen and (max-width: 800px){
    .educationList__title{
        width: 100%;
    }
    .educationList__content__container{
        width: 100%;
    }
    .eeList{
        width:100%;
    }
}